<script>
import useVuelidate from "@vuelidate/core";
import { required, email, minLength, maxLength, helpers } from "@vuelidate/validators";
import { defineComponent } from "@vue/composition-api";
import localizeFilter from '@/filters/localize.filter'

const phoneNumber = helpers.regex(
  /^[+0-9]+$/
)

export default defineComponent({
  props: {

  },
  setup() {
    return { v$: useVuelidate() };
  },
  data() {
    return {
      email: "",
      // email2: "",
      phoneNumber: "",
      password: "",
      password2: "",
      firstName: "",
      lastName: "",
      // middleName: "",
      conditions1Accepted: false,
      conditions2Accepted: true,
    };
  },
  validations() {
    return {
      email: {
        required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
        email: helpers.withMessage(localizeFilter('field_must_be_valid_email_address'), email)
      },
      // email2: { required, email },
      phoneNumber: {
        phoneNumber: helpers.withMessage(localizeFilter('field_must_be_valid_phone_number'), phoneNumber),
        minLength: helpers.withMessage(localizeFilter('min_length', [8]), minLength(8)),
        maxLength: helpers.withMessage(localizeFilter('min_length', [18]), maxLength(18)),
      },
      password: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required), },
      password2: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required), },
      firstName: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required), },
      lastName: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required), },
      // middleName: { required },
    };
  },
  methods: {
    openBot() {
        // window.open('')
    },
    acceptСonditions1() {
        this.conditions1Accepted = !this.conditions1Accepted
    },
    acceptСonditions2() {
        this.conditions2Accepted = !this.conditions2Accepted
    },
    async submitHandler() {
      this.v$.$touch();
      if (this.v$.$error) {
        console.log(this.v$.$error)
        return;
      }
      if (!this.conditions1Accepted || !this.conditions2Accepted) { return }
      // if (this.email != this.email2) {
      //   this.$error({
      //     message: localizeFilter('email_not_match')
      //   })
      //   return 
      // }
      if (this.password != this.password2) {
        this.$error({
          message: localizeFilter('passwords_not_match')
        })
        return 
      }
      const formData = {email: this.email, firstName: this.firstName, phoneNumber: this.phoneNumber, lastName: this.lastName, password: this.password}
      try {
        await this.$store.dispatch('registerPartner', formData)
        await this.$store.dispatch('loginPartner', formData)
        const {scope, email} = await this.$store.dispatch('getProfile')
        if (scope=='not_activated_client') {
          this.$router.push('/confirmEmail?email='+email)
        } else {
          this.$router.push('/cabinet')
        }
        this.$message(localizeFilter('registration_successfull'))
      } catch (e) {
          this.$error(e)
      }
    },
  },
  computed: {
  },
  async mounted() {
    
  }
});
</script>

